.header {
  padding: 15px 0px 5px 0px;
  // padding-bottom: 15px;
  // margin-bottom: 20px;

  @media screen and (max-width: 767px) {
    width: 100%;
    box-shadow: $main-text-color 0px 1px 0px;
  }

  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: space-between;
    // margin-bottom: 20px;
  }
}

.header__title {
  position: relative;
  text-align: center;
}

.header__name {
  letter-spacing: 10px;
  @media screen and (min-width: 768px) {
    padding-top: 10px;
    letter-spacing: 15px;
    font-size: 30px;
  }
  @media screen and (min-width: 1200px) {
    letter-spacing: 20px;
    font-size: 40px;
  }
}

.header__description {
  letter-spacing: 3px;
  @media screen and (min-width: 768px) {
    letter-spacing: 6px;
    font-size: 18px;
  }
  @media screen and (min-width: 1200px) {
    letter-spacing: 12px;
    font-size: 24px;
  }
}

.header__contacts {
  @media screen and (max-width: 767px) {
    display: none;
  }
}
